export function formatNumber(num: number): string {


    //         
    const isNegative: boolean = num < 0;
    const absNum: string = Math.abs(num).toString();

    //         
    let formatted: string = '';
    for (let i = absNum.length - 1; i >= 0; i -= 3) {
        //    ,      
        const space = (i !== absNum.length - 1) ? ' ' : '';
        //  slice      
        const start = Math.max(i - 2, 0);
        const part = absNum.slice(start, i + 1);
        formatted = part + space + formatted;
    }

    //      
    return isNegative ? '-' + formatted : formatted;
}
