export function isWeekend(year: number, month: number, day: number): boolean {
    //   Date.   JavaScript   0,   1  .
    const date = new Date(year, month - 1, day);
    //    : 0  , 6  .
    const dayOfWeek = date.getDay();
    // ,     (6)   (0).
    return dayOfWeek === 0 || dayOfWeek === 6;
}
