<script setup lang="ts">
import {BaseDataType} from "@/store/capacity/types";
import {ToggleType} from "@/components/Capacity/types";
import {computed} from "vue";
import convertMinutesToHours from "../../../utils/convertMinutesToHours";
import store from "@/store";

const props = defineProps<{
  toggleDetail: BaseDataType,
  toggleTypeParam: ToggleType
}>();

const CAPACITY_HEADS = {
  head1: '',
  head2: '',
  line1: '',
  line2: ''
}

const DETAIL_HEADS = {
  head1: '',
  head2: '',
  line1: '',
  line2: ''
}

const words = computed(() => {
  if (props.toggleTypeParam === 'mainData') {
    return CAPACITY_HEADS;
  } else if (props.toggleTypeParam === 'detailData') {
    return DETAIL_HEADS;
  } else {
    console.error('wrong type of toggle');
    return CAPACITY_HEADS;
  }
});

</script>

<template>
  <table class="text-surface-200">
    <tr>
      <th></th>
      <th>{{ words.head1 }}</th>
      <th>{{ words.head2 }}</th>
    </tr>
    <tr v-if="store.getters['meData/getMe'].show_money">
      <td>{{ words.line1 }}</td>
      <td>{{ Math.round(props.toggleDetail.moneyPlan) }}</td>
      <td>{{ props.toggleDetail.moneyFact }}</td>
    </tr>
    <tr>
      <td>{{ words.line2 }}</td>
      <td>{{ convertMinutesToHours(props.toggleDetail.timePlanMin) }}.</td>
      <td>{{ convertMinutesToHours(props.toggleDetail.timeFactMin) }}.</td>
    </tr>
  </table>
</template>

