function getISOWeeksInYear(year: number): 53 | 52 {
    //  ,  1   
    const startOfYear = new Date(year, 0, 1);
    //  ,  31    
    const endOfYear = new Date(year, 11, 31);

    //     1  (0 - , 1 - , ..., 6 - )
    const startDay = startOfYear.getDay();
    //     31 
    const endDay = endOfYear.getDay();

    //    53   ISO 8601
    //        ,     
    //       
    if ((startDay === 1) || (startDay === 0 && endDay === 1) || (startDay === 0 && endDay === 0)) {
        return 53;
    } else {
        return 52;
    }
}

export default getISOWeeksInYear;