function getDaysInMonth(year: number, month: number): number {
    //      JavaScript (0-11)
    const adjustedMonth = month - 1;
    // ,    
    if (adjustedMonth === 11) { // 
        //   Date  " 0"   
        const date = new Date(year + 1, 0, 0);
        return date.getDate();
    } else {
        //   Date  " 0"  
        const date = new Date(year, adjustedMonth + 1, 0);
        return date.getDate();
    }
}

export default getDaysInMonth;